<template>
  <div v-title data-title="个人中心-资源">
    <UserNav></UserNav>
    <el-container direction='vertical' style="margin-right: 200px;">
      <div v-for="(file, index) in user.fileList" :key="index" style="margin: 10px;">
        <el-card class="box-card" shadow="hover">
          <div slot="header" class="clearfix">
            <span>卡片名称</span>
            <el-button type="plain" @click="download(file)">{{file.name}}</el-button>
          </div>
          <div class="text item">
            date
          </div>
        </el-card>
      </div>
    </el-container>
  </div>
</template>

<script>
import UserNav from './UserCenter'
export default {
  name: 'UploadedFiles',
  data () {
    return {
      //fileimgurl: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
      user: {
        fileList:
            [{// 只有文件1可以下载。图片1直接打开了；图片2找不到图片。
              name: '文件1.txt',
              url: '/static/a.txt'
            },
            {
              name: '图片1.png',
              url: 'https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png'
            },
            {
              name: '图片2.png',
              url: '@/assets/logo.png'
            }
            ]
      }
    }
  },
  methods: {
    download (file) {
      let a = document.createElement('a')
      a.href = file.url
      a.download = file.name
      a.click()
    }
  },
  components: {
    UserNav
  }
}
</script>

<style scoped>
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
  }
</style>



